<template>
    <div class="loanCalc">
        <div class="block select">
            <div class="banner">
                <img :src="appConfig.waiting.banner" alt>
                <p class="success">借款申请提交成功</p>
                <p class="wait">请等待审核通知</p>
            </div>
            <p class="title first-title flex flex-between">
                <span>借款金额(元)</span>
                <span :style="[appConfig.mainColor]">{{sel_money}}</span>
            </p>
            <hr>
            <p class="title sec-title flex flex-between">
                <span>借款期限</span>
                <span :style="[appConfig.mainColor]">{{sel_time}}期</span>
            </p>
        </div>
        <div class="block plan">
            <p class="title flex">
                <span>期数</span>
                <span>还款额</span>
            </p>
        </div>
        <div
            class="block flex interest"
            v-for="(item, index) in loanTime"
            :key="index"
            :class="{first: index == 0}"
        >
            <div class="time">
                <div>第{{index + 1}}期</div>
                <div :style="[appConfig.mainColor]">{{item.time}}</div>
            </div>
            <div class="money">
                <div
                    :style="[appConfig.mainColor]"
                >￥{{(Math.round(item.capital * 100) + Math.round(item.interest*100))/100}}</div>
                <div class="math">(本金+息费)￥{{item.capital}}+￥{{item.interest}}</div>
            </div>
        </div>

        <div class="next block text-center">
            <cube-button class="code-btn" @click="goHome" :style="[appConfig.mainBackground]" :primary="true">返回首页</cube-button>
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex'
import { brigeNativeByJavascript } from '../api/Bridge.js';
export default {
    data() {
        return {
            check: true,
            money_option: [3000, 5000, 10000, 20000, 30000, 50000],
            sel_money: parseInt(this.$route.query.money) || 10000,
            time_option: [3, 6, 12],
            sel_time: parseInt(this.$route.query.time) || 12,
            totalInterest: 0, // 总息费
            loanTime: []
        }
    },
    computed: {
        ...mapState(['appConfig'])
    },
    methods: {
        isLeapYear(Year) {
            // 是否闰年
            if ((Year % 4 == 0 && Year % 100 != 0) || Year % 400 == 0) {
                return true
            } else {
                return false
            }
        },
        dateList(time_option) {
            const now = new Date()
            let Year = now.getFullYear()
            let Day = now.getDate()
            let Month = now.getMonth() + 1
            let obj = []
            let lists = []
            for (let i = 1; i < time_option + 1; i++) {
                let m_day
                if (Month === 12) {
                    Month = 1
                    Year++
                } else {
                    Month++
                }
                if (Day > 28 && Month === 2) {
                    m_day = this.isLeapYear(Year) ? 29 : 28
                } else if (Day > 30 && [2, 4, 6, 9, 11].includes(Month)) {
                    m_day = 30
                } else {
                    m_day = Day
                }
                lists.push(Year + '/' + Month + '/' + m_day)
            }
            for (let i = 0; i < lists.length; i++) {
                let times // 天数
                if (i == 0) {
                    times = (Date.parse(lists[0]) - Date.parse(now.toLocaleDateString())) / (24 * 60 * 60 * 1000)
                } else {
                    times = (Date.parse(lists[i]) - Date.parse(lists[i - 1])) / (24 * 60 * 60 * 1000)
                }
                obj.push({ time: lists[i], days: times })
            }
            return obj
        },
        countMoney() {
            // 生成期数和对应的本金和息金
            this.totalInterest = 0
            const capital = (Math.ceil((this.sel_money * 100) / this.sel_time) / 100).toFixed(2) //每期本金
            const interest = day => {
                return (Math.ceil(((this.sel_money * 100 * 0.24) / 365) * day) / 100).toFixed(2) // 利息
            }
            const times = this.dateList(this.sel_time)
            for (let i = 0; i < times.length; i++) {
                times[i].interest = +interest(times[i].days)
                this.totalInterest += +interest(times[i].days)
                times[i].capital = +capital
            }
            this.loanTime = times
        },
        goHome() {
            brigeNativeByJavascript('closeRefundApplication')
        }
    },
    mounted() {
        this.countMoney()
    },
    watch: {
        sel_money(val) {
            this.countMoney()
        },
        sel_time(val) {
            this.countMoney()
        }
    }
}
</script>

